import React from 'react'
import {useSelector} from 'react-redux'
import {useNavigate} from 'react-router-dom'
import Order_Component from '../Components/Order_Component'
import { SelectedOrder } from '../Features/Order/OrderSlice'

export default function Orders() {
  const navigate = useNavigate();
  const order = useSelector(SelectedOrder);

  const handleButton = () =>{
    navigate('/products')
  }
  
  return (
    <div>{order.length === 0?<div className='h-[89vh] justify-center flex flex-col items-center w-full'><p>No Any Order Placed Yet</p>
    <button className="text-blue-900" onClick={()=> handleButton()}>Continue Shopping</button></div>:

    
    <div className=' overflow-hidden over bg-very-light-skin px-20 py-30 gap-3'>
      
      {order.map(function(data){
        return(
        <Order_Component data={data}/>
        )
      })}
      
    </div>}
    </div>
  )
}

