import { createAsyncThunk, createSlice } from "@reduxjs/toolkit"
import { CreateOrder, FetchOrder, Updateorder } from "./OrderAPI";


const initialState = {
   status: 'idle',
   order:[],
}

export const CreateOrderAsync = createAsyncThunk('Order/add', async (userdata)=>{
      console.log('Create order API Called');
      const response = await CreateOrder(userdata);
      return response.data;
})

export const FetchOrderAsync = createAsyncThunk('/Order/fetch', async (userdata)=>{
   console.log("Fetch Order API Called");
   const response = await FetchOrder(userdata);
   return response.data;
})

export const UpdateorderAsync = createAsyncThunk('Order/update', async (userdata)=>{
   const response = await Updateorder(userdata);
   return response.data;
})

export const OrderSlice  = createSlice({
   name:'Order',
   initialState,
   reducers:{},
   extraReducers:  (builder)=>{
      builder
      .addCase(CreateOrderAsync.pending,(state)=>{
         state.status = 'pending';
      })
      .addCase(CreateOrderAsync.fulfilled,(state,actions)=>{
         state.status = 'idle';
         state.order.push(actions.payload);
      })
      .addCase(FetchOrderAsync.pending,(state)=>{
         state.status = 'pending';
      })
      .addCase(FetchOrderAsync.fulfilled,(state,actions)=>{
         state.status = 'idle';
         state.order = actions.payload;
      })
      .addCase(UpdateorderAsync.pending,(state)=>{
         state.status = 'pending';
      })
      .addCase(UpdateorderAsync.fulfilled,(state,actions)=>{
         state.status = 'idle';
         state.order = actions.payload;
      })
   }
})

export const SelectedOrder = (state) => state.Order.order;

export default OrderSlice.reducer;