import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  FetchProduct,
  FetchProductbyCategory,
  FetchProductbyID,
} from "./ProductAPI";

const initialState = {
  status: "idle",
  products: [],
  product: [],
  similiarProduct: [],
  totalProduct: 0,
};

export const FetchProductsAsync = createAsyncThunk(
  "Product/FetchProduct",
  async ({ sort, pagination }) => {
    console.log("working in API slice");
    const response = await FetchProduct(sort, pagination);
    return response.data;
  }
);

export const FetchProductbyIDAsync = createAsyncThunk(
  "Product/FetchProductbyID",
  async (userdata) => {
    console.log("Fetch Product By ID API Called");
    const response = await FetchProductbyID(userdata);
    console.log("wdinwerd", response.data);
    return response.data;
  }
);
export const FetchProductbyCategoryAsync = createAsyncThunk(
  "Product/FetchProductbyCategory",
  async (userdata) => {
    console.log("Fetch Product By Category API Called");
    const response = await FetchProductbyCategory(userdata);
    return response.data;
  }
);

export const ProductSlice = createSlice({
  name: "Product",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(FetchProductsAsync.pending, (state) => {
        state.status = "pending";
      })
      .addCase(FetchProductsAsync.fulfilled, (state, actions) => {
        state.status = "idle";
        state.products = actions.payload.products;
        state.totalProduct = actions.payload.totalitems;
      })
      .addCase(FetchProductbyIDAsync.pending, (state) => {
        state.status = "pending";
      })
      .addCase(FetchProductbyIDAsync.fulfilled, (state, actions) => {
        state.status = "idle";
        state.product = actions.payload;
      })
      .addCase(FetchProductbyCategoryAsync.pending, (state)=>{
        state.status = 'Pending';
      })
      .addCase(FetchProductbyCategoryAsync.fulfilled,(state,actions)=>{
        state.status = 'idle';
        state.similiarProduct = actions.payload;
      });
  },
});

export const selectedProduct = (state) => state.Product.products;
export const SelectTotalItems = (state) => state.Product.totalProduct;
export const SelectProductbyID = (state) => state.Product.product;
export const SelectedSimilarProduct = (state)=> state.Product.similiarProduct;

export default ProductSlice.reducer;
