
export async function AddAddress(userdata){
   return new Promise(async (resolve)=>{
      const response = await fetch('/address/add',{
         method:'POST',
         body:JSON.stringify(userdata),
         headers:{'content-type':'application/json'}
      })
      const data = response.json();
      resolve({data})
   })
}

export async function FetchAddress(userdata){
   return new Promise(async (resolve)=>{
      const response = await fetch('/address',{
         method:'POST',
         body:JSON.stringify(userdata),
         headers:{'content-type':'application/json'}
      })
      const data = response.json();
     
      resolve({data})
   })
}

export async function deleteCart(userdata){
   return new Promise(async (resolve)=>{
      const response = await fetch(`/address/delete/` + userdata,{
         method:'DELETE',
         headers:{'content-type':'application/json'}
      })
      const data = response.json();
      resolve({data});
   })
}