export async function AddtoCart(userdata) {
  return new Promise(async (resolve) => {
    const response = await fetch("/cart/add", {
      method: "POST",
      body: JSON.stringify(userdata),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    resolve({ data });
  });
}

export async function FetchCart(userdata) {
  return new Promise(async (resolve) => {
    const response = await fetch("/cart/Cart", {
      method: "POST",
      body: JSON.stringify(userdata),
      headers: { "content-type": "application/json" },
    });

    const data = response.json();
    
    resolve({data});
  });
}

export async function UpdateCart(userdata) {
  return new Promise(async (resolve) => {
    const response = await fetch("/cart/update", {
      method: "POST",
      body: JSON.stringify(userdata),
      headers: { "content-type": "application/json" },
    });

    const data = response.json();
    resolve({ data });
  });
}

export async function DeleteCart(userdata) {
  return new Promise(async (resolve) => {
    console.log(userdata,"WOrjie")
    const response = await fetch(`/cart/delete/` + userdata, {
      method:"DELETE",
      headers:{'content-type':'application/json'}
    });

    const data = response.json();
    resolve({ data });
  });
}
