import React, { useState, useEffect } from "react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useDispatch } from "react-redux";
import { DeleteCartAsync, UpdatecartAsync } from "../Features/Cart/CartSlice";

export default function Cart_Component({ data }) {
  const [qunatity, setQunatity] = useState(data.quantity);
  const dispatch = useDispatch();
  const arr = [];
  const product = data.product;
  const IncreaseQunatity = () => {
    setQunatity(qunatity + 1);
    toast(`The Qunatity of Product is Increased`);
  };
  const DecrementQunatity = () => {
    if (qunatity === 1) {
      alert("Minimum qunatity is 1 ");
      return;
    }
    setQunatity(qunatity - 1);
    toast(`The Qunatity of Product is Decreased `);
  };

  const handleremove = () => {
    dispatch(DeleteCartAsync(data._id));
  };
  for (let i = 0; i < product.rating; i++) {
    arr.push(1);
  }

  useEffect(() => {
    dispatch(UpdatecartAsync({ id: data._id, quantity: qunatity }));
  }, [dispatch, qunatity]);
  return (
    <div>
      <ToastContainer
        position="top-right"
        autoClose={2000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
      <div className="grid grid-cols-2">
        <img src={product.thumbnail} className="w-60 py-4" />
        <div className="py-2  text-gray-green">
          <p className="chathura-regular  text-3xl ">{product.title}</p>
          <div className="pt-1">
            {arr.map(() => (
              <span className="text  text-dark-orange-1000 text-xl">
                &#9733;{" "}
              </span>
            ))}
            <span className="charmonman-regular text-sm text-dark-orange-1000">
              {product.reviews} reviews
            </span>
          </div>
          <div className="text-lg">
            <span className="pr-2 line-through text-gray-green sansita-regular">
              {" "}
              &#x20B9;{product.price}
            </span>
            <span className="pr-2 text-gray-green sansita-regular">
              {" "}
              &#x20B9;
              {Math.round(
                product.price -
                  (product.price * product.discountPercentage) / 100
              )}
            </span>
            <span className="text-dark-orange-1000 charmonman-regular text-sm">
              {product.discountPercentage}% Save
            </span>
          </div>
          <p className="rufina-regular pt-2">
            {product.description.slice(0, 80)}...
          </p>
          <div className="flex gap-10 pt-3 ">
            <div className="flex bg-button-color rounded-md px-2 py-1">
              <p
                className="bg-white pb-1 px-2 rounded cursor-pointer"
                onClick={() => IncreaseQunatity()}
              >
                +
              </p>{" "}
              <p className="px-2">{qunatity}</p>{" "}
              <p
                className="bg-white pb-1 px-3 rounded cursor-pointer"
                onClick={() => DecrementQunatity()}
              >
                -
              </p>
            </div>
            <button
              className="px-6 shadow-2xl py-1 bg-button-color text-gray-green rounded-full sansita-regular "
              onClick={() => handleremove()}
            >
              Remove
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
