import React, { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {  loggeduser } from '../Authentication/AuthSlice'
import { useNavigate } from 'react-router-dom';
import { FetchCartAsync } from '../Features/Cart/CartSlice';
import { FetchAddressAsync } from '../Features/Address/AddressSlice';
import { FetchOrderAsync } from '../Features/Order/OrderSlice';

export default function Check_nav() {
   const user = useSelector(loggeduser);
   const dispatch = useDispatch()
   const navigate = useNavigate();
   

   function check(){
      if(user){
        dispatch(FetchCartAsync({id:user.id}))
        dispatch(FetchOrderAsync({id:user.id}))
        dispatch(FetchAddressAsync({id:user.id}))
         navigate('/')
         return ;
      }
      else{
        navigate('/login')
      }
      
   }
   useEffect(()=>{
      check()
   },[user])
  return (
    <div>Check_nav</div>
  )
}
