//Function TO Create New User
export async function CreateAccount(userdata) {
  console.log("CreateAccount API Called")
  return new Promise(async (resolve) => {
    const response = await fetch("/user/add", {
      method: "POST",
      body: JSON.stringify(userdata),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    console.log(data);
    resolve({ data });
  });
}

//Function To Login Existing User
export async function LoginUser(userdata) {
  return new Promise(async (resolve, reject) => {
    try {
      const response = await fetch("/user/login", {
        method: "POST",
        body: JSON.stringify(userdata),
        headers: { "content-type": "application/json" },
      });
      console.log(response)
      if (response.ok) {
        const data = await response.json();
        resolve({ data });
      } else {
        const data = await response.json();
        reject({ data });
      }
    } catch (err) {
      reject(err);
    }
  });
}

//Function To CheckUser

export async function CheckUser(userdata) {
  console.log("Working in checkuser")
  return new Promise(async (resolve, reject) => {
    try {
      console.log('Code in try')
      const response = await fetch("/user/check");
      if(response.ok){
        const data = await response.data();
        resolve({data});
      }
      else{
        const error = await response.text()
        reject(error)
      }
    
    } catch (err) {
      reject(err);
    }
  });
}

//Function To Update Password

export async function UpdatePassword(userdata){
  return new Promise(async (resolve,reject) =>{
    try {
    const response = await fetch('/Auth/updatepassword',{
      method:'POST',
      body:JSON.stringify(userdata),
      headers:{'content-type':'application/json'}
    })
    const doc = response.data();
    resolve(doc);
  }
  catch (err){
    reject(err)
  }
  })
}
