import React from 'react'
import NavBar from '../Naviagations/NavBar'
import Profile_Navigation from '../Naviagations/Profile_Navigation'
import Orders from '../Secondary_Profile_Page/Orders'
import Address from '../Secondary_Profile_Page/Address';
import { useSelector } from 'react-redux';
import { Profile_nav } from '../Authentication/AuthSlice';
import Edit_Profile from '../Secondary_Profile_Page/Edit_Profile';
import Notification from '../Secondary_Profile_Page/Notification';
import Setting from '../Secondary_Profile_Page/Setting';

export default function Profile() {
  const active = useSelector(Profile_nav);
  return (
    <div>
     
      <div className="fixed w-full">
      <NavBar />
      </div>
      <div className='grid grid-cols-4 pt-24'>
      <div></div>
        <div className="fixed w-1/4">
          
        <Profile_Navigation/>
        </div>
        <div className='col-span-3 h-full' style={{display:active==="order"?'block':'none'}}>
        <Orders ></Orders>
        </div>
        <div className='col-span-3 h-full' style={{display:active==="Address"?'block':'none'}}>
        <Address></Address>
        </div>
        <div className='col-span-3 h-full' style={{display:active==="Profile"?'block':'none'}}>
        <Edit_Profile></Edit_Profile>
        </div>
        <div className='col-span-3 h-full' style={{display:active==="Notification"?'block':'none'}}>
        <Notification></Notification>
        </div>
        <div className='col-span-3 h-full' style={{display:active==="Setting"?'block':'none'}}>
        <Setting></Setting>
        </div>
        
      </div>
    </div>
  )
}
