

export  function FetchProduct(sort,pagination) {
  console.log('working 1', sort, pagination)
   // sort = {_sort:"price",_order="desc"}
  // pagination = {_page:1,_limit=10}
  let querystring = '';
  for (let key in sort){
    console.log(sort[key])
    querystring += `${key}=${sort[key]}&`;
  }
 

  for(let key in pagination){
    console.log('Pagination working')
    querystring += `${key}=${pagination[key]}&`
  }
  console.log('workingwenv',querystring)
  return new Promise(async (resolve) => {
    const response = await fetch(`/product/products?` + querystring);
    const data = await response.json();
    console.log('working',data)
   
    resolve({data:{products: data.product , totalitems :+data.totalitem}});
  });
}

export async function FetchProductByFilter(userdata) {
  // filter = {"category":"filter"}
  
  return new Promise(async (resolve) => {
    const response = await fetch("/product/fetchproduct", {
      method: "POST",
      body: JSON.stringify(userdata),
      headers: { "content-type": "application/json" },
    });
    const data =  await response.json();
    resolve({ data });
  });
}

export async function FetchProductbyID(userdata){
  return new Promise (async (resolve)=>{
  const response = await fetch('/product/Fetchproductbyid',{
    method:'POST',
    body:JSON.stringify(userdata),
    headers:{'content-type':'application/json'},
  })
  const data = await response.json();
  resolve({data});
})
}

