import React from "react";
import "../css/main.css";

export default function Order_Component({ data }) {
  const product = data;
  const arr = [];
  for (let i = 0; i < product.product.rating; i++) {
    arr.push(1);
  }
  return (
    <div className="grid grid-cols-3 bg-light-skin px-10 py-8 mb-10 mt-10">
      <div>
        <img
          src={product.product.thumbnail}
          className=" w-60 h-60"
          alt={product.product.title}
        />
      </div>
      <div className="">
        <h2 className="  text-gray-green chathura-bold text-3xl">
          {product.product.title}
        </h2>
        {arr.map(() => (
          <span className="text text-light-orange text-2xl">&#9733;</span>
        ))}
        <span className="text-light-orange  ml-1 sansita-regular-italic">
          <span className="">{product.reviews} </span>reviews
        </span>
        <div className="flex gap-3">
          <p className="flex text-gray-green sansita-regular">
            ₹<p className="line-through">{product.Price}</p>
          </p>
          {/* <p className="flex text-gray-green sansita-bold">
            {" "}
            ₹
            <p>
              {Math.round(
                product.price -
                  (product.price / 100) * product.discountPercentage
              )}
            </p>
          </p>
          <p className="gap-1 text-text-orange sansita-regular-italic">
            save {product.discountPercentage}%
          </p> */}
        </div>
        <div className="flex sansita-regular py-2">
          <p className=" text-gray-green">Payment:-</p>
          <p className=" text-geruwa">{product.payment_method}</p>
        </div>
        <div className="flex sansita-regular py-2">
          <p className=" text-gray-green">Address:-</p>
          <p className=" text-geruwa">
            {product.Address.Street} {product.Address.district}{" "}
            {product.Address.State}
          </p>
        </div>
        <div className="flex sansita-regular py-2">
          <p className=" text-gray-green">Contact Number</p>
          <p className=" text-geruwa">
            +{product.Address.Primary_contact_Number}
          </p>
        </div>
        <div className=" hover:bg-white sansita-bold hover:text-gray-green text-white flex w-fit px-8 py-1 ml-9 mt-2 cursor-pointer justify-center items-center rounded-full bg-gray-green text-lg">
          Cancel
        </div>
      </div>
      <div className="  flex flex-col items-end">
        <div className="flex sansita-regular mt-4">
          <p className=" text-gray-green">Delivery up-to :-</p>
          <p className=" text-geruwa">{product.order_date.slice(0, 10)}</p>
        </div>
        <div className="flex sansita-regular mt-2">
          <p className=" text-gray-green">Ordered on :-</p>
          <p className=" text-geruwa">{product.Dilervery_date.slice(0, 10)}</p>
        </div>
        <div className="flex sansita-regular mt-40">
          <p className=" text-gray-green">Order Status:-</p>
          <p className=" text-geruwa">{product.status}</p>
        </div>
      </div>
    </div>
  );
}
