import { createAsyncThunk, createSlice } from "@reduxjs/toolkit"
import { AddAddress, FetchAddress, deleteCart } from "./AddressAPI";

const initialState = {
   status:'idle',
   address:[],
}

export const AddAddressAsync = createAsyncThunk(
   'Address/add',
   async (userdata)=>{
      console.log('Add Address API Called');
      const response = await AddAddress(userdata);
      return response.data;
   }
)

export const FetchAddressAsync = createAsyncThunk('Address/fetch', async (userdata)=>{
   console.log("Fetch Address API Called");
   const response = await FetchAddress(userdata);
   console.log(response)
   return response.data;
})

export const DeleteAddressAsync = createAsyncThunk('Address/delete', async (userdata)=>{
   console.log('Delete Address API Called');
   const response = await deleteCart(userdata);
   return response.data;
})


export const AddressSlice = createSlice({
   name:'Address',
   initialState,
   reducers:{},
   extraReducers: (builder)=>{
      builder
      .addCase(AddAddressAsync.pending,(state)=>{
         state.status = 'pending';
      })
      .addCase(AddAddressAsync.fulfilled,(state,actions)=>{
         state.status = 'idle';
         state.address.push(actions.payload);
      })
      .addCase(DeleteAddressAsync.pending,(state)=>{
         state.status = 'pending';
      })
      .addCase(DeleteAddressAsync.fulfilled,(state, actions)=>{
         state.status = 'deleted';
         const index = state.address.findIndex(item => item._id === actions.payload)
         state.address.splice(index,1);
      })
      .addCase(FetchAddressAsync.pending,(state)=>{
         state.status = 'pending';
      })
      .addCase(FetchAddressAsync.fulfilled, (state,actions)=>{
         state.status = 'idle';
         state.address = actions.payload;
      })
   }
})

export const SelectedAddress = (state)=> state.Address.address;


export default AddressSlice.reducer;