import { RouterProvider, createBrowserRouter } from "react-router-dom";
import Home from "./main_pages/Home";
import Login from "./Authentication/Login";
import SignUp from "./Authentication/SignUp";
import { AuthUserContextProvider } from "./Context1/AuthUserContext";
import Products from "./main_pages/Products";
import Jewellery from "./main_pages/Jewellery";
import Protected_Route from "./Naviagations/Protected_Route";
import Cart from "./main_pages/Cart";
import Profile from "./main_pages/Profile";


import Check_nav from "./Naviagations/Check_nav";
import { useDispatch } from "react-redux";
import { useEffect } from "react";
import { FetchProductsAsync } from "./Products/ProductSlice";
import ProducrDetails from "./Products/ProducrDetails";
import Order from "./Features/Order/Component/Order";
import Success from "./Components/Success";





const router = createBrowserRouter([
  {
    path:'/',
    element: (<Home></Home>)
  },
  {
    path:'/login',
    element:(<Login></Login>)
  },
  {
    path:'/signup',
    element:(<SignUp></SignUp>)
  },
  {
    path:'/products',
    element:(<Products></Products>)
  },
  {
    path:'/jewellery',
    element:(<Jewellery></Jewellery>)
  },
  {
    path:'/cart',
    element:(<Protected_Route><Cart></Cart></Protected_Route>)
  },
  {
    path:'/profile',
    element:(<Protected_Route><Profile></Profile></Protected_Route>)
  },
  {
    path:'/check',
    element:(<Check_nav></Check_nav>)
  },
  {
    path:'/products/details',
    element:(<ProducrDetails></ProducrDetails>)
  },
  {
    path:'/order',
    element:(<Protected_Route><Order></Order></Protected_Route>)
  },
  {
    path:'/sucess',
    element:(<Success></Success>)

  }
  

 
])

function App() {
  const dispatch = useDispatch();

  useEffect(()=>{
    dispatch(FetchProductsAsync())
  },[])
  return (
    <div className="App">
     
     <AuthUserContextProvider> 
     <RouterProvider router={router}/>
      </AuthUserContextProvider> 
      
    </div>
  );
}

export default App;
