import React, { useState } from "react";
import { loadStripe } from "@stripe/stripe-js";
import { useLocation } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import NavBar from "../../../Naviagations/NavBar";
import { SelectedAddress } from "../../Address/AddressSlice";
import { CreateOrderAsync } from "../OrderSlice";
import { loggeduser } from "../../../Authentication/AuthSlice";
import { SelectedCart } from "../../Cart/CartSlice";

export default function Order() {
  const date = new Date();
  const dilevry_date = new Date();
  dilevry_date.setDate(date.getDate() + 10);
  const [newAdress, setNewAddress] = useState(false);
  const [payment_method, setPayment_Method] = useState();
  const [Slected_Address, Set_Selected_Address] = useState();
  const data = useLocation();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const user = useSelector(loggeduser);
  const address = useSelector(SelectedAddress);
  const cart = useSelector(SelectedCart)
  const makePayment = async ()=>{
    
    const stripe = await loadStripe("pk_test_51NNa7MSDHK09cQh7prrFvWdcVSBJouLqdW6i1IjvVOVDaGsbu40y1paM7NGnxP930pxzlTylMK6ZebHXB6ncSLIY00DABWyl9y")

    const body = {
      products:data
    }
    const response = await fetch('/create-checkout-sesssion',{
      method:"POST",
      body:JSON.stringify(body),
      headers:{'content-type':"application/json"}
    })
    const sesssion = await response.json();
    dispatch(
      CreateOrderAsync({
        Address: Slected_Address,
        payment_method: payment_method,
        product: data.state.id,
        user: user.id,
        Price: data.state.price,
        discount: data.state.discountPercentage,
        quantity: 1,
        status: "pending",
        order_date: date,
        Dilervery_date: dilevry_date,
      })
    );
    const result = stripe.redirectToCheckout({
      sessionId:sesssion.id
    })
    if(result.error){
      console.log(result.error)
    }
  }

  const CreateOrder = () => {

    if (!Slected_Address) {
      toast("Please Select Address");
      return;
    }
    if (!payment_method) {
      toast("please Slecte Payment Method");
      return;
    }
    if (Slected_Address && payment_method) {
      if(payment_method =="Card"){
        console.log("Card");
        makePayment()
        
        return ;
      }
      dispatch(
        CreateOrderAsync({
          Address: Slected_Address,
          payment_method: payment_method,
          product: data.state.id,
          user: user.id,
          Price: data.state.price,
          discount: data.state.discountPercentage,
          quantity: 1,
          status: "pending",
          order_date: date,
          Dilervery_date: dilevry_date,
        })
      );
      toast("Your Order had been Placed");
      setTimeout(() => {
        navigate("/profile");
      }, 200);
      return;
    }
  };

  return (
    <div className="bg-light-orange-100">
      <NavBar />
      <ToastContainer
        position="top-right"
        autoClose={2000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />

      <div className="px-40 py-12">
        <form>
          <div className="space-y-12">
            <h2
              className="text-base  font-semibold leading-7 rounded-full shadow-2xl cursor-pointer bg-gray-green-500 px-12 py-2 text-white  w-60"
              style={{ display: !newAdress ? "block" : "none" }}
              onClick={() => setNewAddress(!newAdress)}
            >
              Add New Address
            </h2>
            <div
              className="border-b border-gray-900/10 pb-12"
              style={{ display: newAdress ? "block" : "none" }}
            >
              <p className="mt-1 text-sm leading-6 text-gray-600">
                Use a permanent address where you can receive mail.
              </p>

              <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                <div className="sm:col-span-3">
                  <label
                    htmlFor="first-name"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    First name
                  </label>
                  <div className="mt-2">
                    <input
                      type="text"
                      name="first-name"
                      id="first-name"
                      autoComplete="given-name"
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>

                <div className="sm:col-span-3">
                  <label
                    htmlFor="last-name"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    Last name
                  </label>
                  <div className="mt-2">
                    <input
                      type="text"
                      name="last-name"
                      id="last-name"
                      autoComplete="family-name"
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>

                <div className="sm:col-span-4">
                  <label
                    htmlFor="email"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    Email address
                  </label>
                  <div className="mt-2">
                    <input
                      id="email"
                      name="email"
                      type="email"
                      autoComplete="email"
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>

                <div className="sm:col-span-3">
                  <label
                    htmlFor="country"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    Country
                  </label>
                  <div className="mt-2">
                    <select
                      id="country"
                      name="country"
                      autoComplete="country-name"
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:max-w-xs sm:text-sm sm:leading-6"
                    >
                      <option>United States</option>
                      <option>Canada</option>
                      <option>Mexico</option>
                    </select>
                  </div>
                </div>

                <div className="col-span-full">
                  <label
                    htmlFor="street-address"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    Street address
                  </label>
                  <div className="mt-2">
                    <input
                      type="text"
                      name="street-address"
                      id="street-address"
                      autoComplete="street-address"
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>

                <div className="sm:col-span-2 sm:col-start-1">
                  <label
                    htmlFor="city"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    City
                  </label>
                  <div className="mt-2">
                    <input
                      type="text"
                      name="city"
                      id="city"
                      autoComplete="address-level2"
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>

                <div className="sm:col-span-2">
                  <label
                    htmlFor="region"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    State / Province
                  </label>
                  <div className="mt-2">
                    <input
                      type="text"
                      name="region"
                      id="region"
                      autoComplete="address-level1"
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>

                <div className="sm:col-span-2">
                  <label
                    htmlFor="postal-code"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    ZIP / Postal code
                  </label>
                  <div className="mt-2">
                    <input
                      type="text"
                      name="postal-code"
                      id="postal-code"
                      autoComplete="postal-code"
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>
              </div>
            </div>

            <div className="border-b border-gray-900/10 pb-12">
              <h2 className="text-base font-semibold leading-7 text-gray-900">
                Choose Address for Order Your Product{" "}
              </h2>

              <div className="mt-4 space-y-10">
                <fieldset>
                  <div className="mt-6 space-y-6">
                    {address.map(function (data, index) {
                      return (
                        <div className="relative flex gap-x-3 py-4  px-10 w-max">
                          <div className="flex h-6 items-center ">
                            <input
                              id="candidates"
                              name="Address"
                              type="radio"
                              onClick={() => Set_Selected_Address(data._id)}
                              className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                            />
                          </div>
                          <div className="grid grid-cols-2  px-10  ">
                            <div className="">
                              <div className="flex sansita-regular py-2">
                                <p className=" text-gray-green">Name:-</p>
                                <p className=" text-geruwa">
                                  {data.user.username}
                                </p>
                              </div>
                              <div className="flex sansita-regular py-2">
                                <p className=" text-gray-green">
                                  Primary Contact Number:-
                                </p>
                                <p className=" text-geruwa">
                                  {data.Primary_contact_Number}
                                </p>
                              </div>
                              <div className="flex sansita-regular py-2">
                                <p className=" text-gray-green">Address:-</p>
                                <p className=" text-geruwa">
                                  {data.Street}&nbsp; {data.district} (
                                  {data.State})
                                </p>
                              </div>
                              <div className="flex sansita-regular py-2">
                                <p className=" text-gray-green">Zip Code:-</p>
                                <p className=" text-geruwa">{data.ZipCode}</p>
                              </div>
                            </div>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                </fieldset>
                <fieldset>
                  <legend className="text-sm font-semibold leading-6 text-gray-900">
                    Choose Payment Method
                  </legend>
                  <p className="mt-1 text-sm leading-6 text-gray-600">
                    These are delivered via SMS to your mobile phone.
                  </p>
                  <div className="mt-6 space-y-6">
                    <div className="flex items-center gap-x-3">
                      <input
                        id="push-everything"
                        name="Payment"
                        onClick={() => setPayment_Method("COD")}
                        type="radio"
                        className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                      />
                      <label
                        htmlFor="push-everything"
                        className="block text-sm font-medium leading-6 text-gray-900"
                      >
                        Cash On Dilvery
                      </label>
                    </div>
                    <div className="flex items-center gap-x-3">
                      <input
                        id="push-email"
                        name="Payment"
                        type="radio"
                        onClick={() => setPayment_Method("Card")}
                        className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                      />
                      <label
                        htmlFor="push-email"
                        className="block text-sm font-medium leading-6 text-gray-900"
                      >
                        Card Payment
                      </label>
                    </div>
                  </div>
                </fieldset>
              </div>
            </div>
          </div>

          <div className="mt-6 flex items-center justify-end gap-x-6">
            <button
              type="button"
              className="text-sm font-semibold leading-6 text-gray-900"
            >
              Cancel
            </button>
            <button
              type="button"
              className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              onClick={() => CreateOrder()}
            >
              Place Order
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
