import React from "react";
import {useDispatch} from 'react-redux'
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { DeleteAddressAsync } from "../Features/Address/AddressSlice";

export default function Address_Component({Address}) {
  const dispatch = useDispatch();

 const DeleteAddress =()=>{
  dispatch(DeleteAddressAsync(Address._id))
  toast("Address Was Deleted");
  return ;

  

  }
  return (
    <div> <ToastContainer
    position="top-right"
    autoClose={2000}
    hideProgressBar={false}
    newestOnTop={false}
    closeOnClick
    rtl={false}
    pauseOnFocusLoss
    draggable
    pauseOnHover
    theme="light"
  />
    <div className="grid grid-cols-3 bg-light-skin px-10 py-8 mb-10 mt-10">
      
      <div className="">
        <div className="flex sansita-regular py-2">
          <p className=" text-gray-green">Name:-</p>
          <p className=" text-geruwa">{Address.user.username}</p>
        </div>
        <div className="flex sansita-regular py-2">
          <p className=" text-gray-green">Primary Contact Number:-</p>
          <p className=" text-geruwa">{Address.Primary_contact_Number}</p>
        </div>
        <div className="flex sansita-regular py-2">
          <p className=" text-gray-green">Secondary Contact Number</p>
          <p className=" text-geruwa">+{Address.Secondary_Contact_Number}</p>
        </div>
       
      </div>
      <div className="  flex flex-col items-end">
        <div className="flex sansita-regular mt-4">
          <p className=" text-gray-green">Street:-</p>
          <p className=" text-geruwa">{Address.Street}</p>
        </div>
        <div className="flex sansita-regular mt-4">
          <p className=" text-gray-green">District:-</p>
          <p className=" text-geruwa">{Address.district}</p>
        </div>
        <div className="flex sansita-regular mt-4">
          <p className=" text-gray-green">State:-</p>
          <p className=" text-geruwa">{Address.State}</p>
        </div>
        <div className="flex sansita-regular mt-2">
          <p className=" text-gray-green">Pin Code:-</p>
          <p className=" text-geruwa">{Address.ZipCode}</p>
        </div>
        
      </div>
      <div className="flex flex-col justify-center items-end">
        <div className=" hover:bg-white sansita-bold hover:text-gray-green text-white flex w-fit px-8 py-1 ml-9 mt-2 cursor-pointer justify-center items-center rounded-full bg-gray-green text-lg">
          Edit Address 
        </div>
        <div className=" hover:bg-gray-green sansita-bold hover:text-white text-gray-green flex w-fit px-8 py-1 ml-9 mt-2 cursor-pointer justify-center items-center rounded-full bg-white text-lg" onClick={()=> DeleteAddress()}>
          Delete Address
        </div>
        </div>
    </div>
    </div>
  );
}
