import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Address_Component from "../Components/Address_Component";
import { loggeduser } from "../Authentication/AuthSlice";
import {
  AddAddressAsync,
  SelectedAddress,
} from "../Features/Address/AddressSlice";

export default function Address() {
  const [addAdress, SetAdrees] = useState(false);
  const [name, setName] = useState();
  const [primary_Contact, setprimary_Contact] = useState();
  const [secondry_Contact, setSecondry_Contact] = useState();
  const [country, setCountry] = useState("India");
  const [street, setStreet] = useState();
  const [city, setCity] = useState();
  const [state, SetState] = useState();
  const [zip, setZip] = useState();
 

  const Addresses = useSelector(SelectedAddress);

  const user = useSelector(loggeduser);
  const dispatch = useDispatch();
  const handleSubmit = (e) => {
    e.preventDefault();
    if (!name) {
      toast("Please Fill Name");
      return;
    }
    if(!primary_Contact){
      toast("Please Fill Primary Contact Number")
      return ;
     }
     if(!street){
      toast("Please Fill Street");
      return ;
     }
     if(!city){
      toast("Please Fill City");
      return ;
     }
     if(!state){
      toast("Please Fill State");
      return ;
     }
     if(!zip){
      toast("Please Fill Zip Code");
      return ;
     }
    dispatch(
      AddAddressAsync({
        user: user.id,
        Street: street,
        State: state,
        district: city,
        ZipCode: zip,
        Primary_contact_Number: primary_Contact,
        Secondary_contact_Number: secondry_Contact,
      })
    );
    document.getElementById('Myfrom').reset();
    SetAdrees(false);
    return ;
    
  };
  return (
    <div>
      <ToastContainer 
        position="top-right"
        autoClose={2000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />{!Addresses?<div>
      <p>No Any Address Is Added.</p>
      <button>
        Add New Address
      </button>
    </div>:
      
    <div className="  bg-very-light-skin">
      <div>
        <div style={{ display: !addAdress ? "block" : "none" }}>
          <div
            className=" flex justify-end  px-32 py-12"
            onClick={() => SetAdrees(true)}
          >
            <svg
              width="34"
              height="34"
              viewBox="0 0 34 34"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                opacity="0.4"
                d="M9.5 17H26.5"
                stroke="black"
                stroke-width="1.5"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
              <path
                d="M18 25.5V8.5"
                stroke="black"
                stroke-width="1.5"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg>{" "}
            <p className="sansita-regular text-xl text-gray-green cursor-pointer">
              Add New Address
            </p>
          </div>

          <div>
            {Addresses.map(function (data) {
              return (
                <div className="px-32">
                  <Address_Component Address={data} />
                </div>
              );
            })}
          </div>
        </div>
        <form
          style={{ display: addAdress ? "block" : "none" }}
          onSubmit={(e) => handleSubmit(e)}

          id="Myfrom"
        >
          <div className="space-y-12 px-32">
            <div className="border-b border-gray-900/10 pb-12 pt-10">
              <h2 className="text-base font-semibold leading-7 text-gray-900">
                Add New Address
              </h2>
              <p className="mt-1 text-sm leading-6 text-gray-600">
                Use a permanent address where you can Pickup Order.
              </p>

              <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                <div className="sm:col-span-3">
                  <label
                    htmlFor="first-name"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    Full name
                  </label>
                  <div className="mt-2">
                    <input
                      type="text"
                      name="first-name"
                      id="first-name"
                      autoComplete="given-name"
                      onChange={(e) => setName(e.target.value)}
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>

                <div className="sm:col-span-4">
                  <label
                    htmlFor="email"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    Primary Contact Number
                  </label>
                  <div className="mt-2">
                    <input
                      id="email"
                      name="email"
                      type="tel"
                      onChange={(e) => setprimary_Contact(e.target.value)}
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>
                <div className="sm:col-span-4">
                  <label
                    htmlFor="email"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    Secondary Contact Number
                  </label>
                  <div className="mt-2">
                    <input
                      id="text"
                      name="email"
                      type="tel"
                      onChange={(e) => setSecondry_Contact(e.target.value)}
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>

                <div className="sm:col-span-3">
                  <label
                    htmlFor="country"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    Country
                  </label>
                  <div className="mt-2">
                    <select
                      id="country"
                      name="country"
                      autoComplete="country-name"
                      onChange={(e) => setCountry(e.target.value)}
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:max-w-xs sm:text-sm sm:leading-6"
                    >
                      <option>India</option>
                    </select>
                  </div>
                </div>

                <div className="col-span-full">
                  <label
                    htmlFor="street-address"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    Street address
                  </label>
                  <div className="mt-2">
                    <input
                      type="text"
                      name="street-address"
                      id="street-address"
                      autoComplete="street-address"
                      onChange={(e) => setStreet(e.target.value)}
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>

                <div className="sm:col-span-2 sm:col-start-1">
                  <label
                    htmlFor="city"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    City
                  </label>
                  <div className="mt-2">
                    <input
                      type="text"
                      name="city"
                      id="city"
                      autoComplete="address-level2"
                      onChange={(e) => setCity(e.target.value)}
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>

                <div className="sm:col-span-2">
                  <label
                    htmlFor="region"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    State / Province
                  </label>
                  <div className="mt-2">
                    <input
                      type="text"
                      name="region"
                      id="region"
                      autoComplete="address-level1"
                      onChange={(e) => SetState(e.target.value)}
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>

                <div className="sm:col-span-2">
                  <label
                    htmlFor="postal-code"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    ZIP / Postal code
                  </label>
                  <div className="mt-2">
                    <input
                      type="text"
                      name="postal-code"
                      id="postal-code"
                      autoComplete="postal-code"
                      onChange={(e) => setZip(e.target.value)}
                      className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="mt-6 flex items-center justify-end gap-x-6 px-32 py-4">
            <button
              type="button"
              className="text-sm font-semibold leading-6 text-gray-900"
              onClick={() => SetAdrees(false)}
            >
              Cancel
            </button>
            <button
              type="submit"
              className="rounded-md bg-gray-green px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-gray-green-500  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            >
              Save
            </button>
          </div>
        </form>
      </div>
    </div>}
    </div>
  );
}
