import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { FetchProductbyCategoryAsync, FetchProductbyIDAsync, SelectProductbyID, SelectedSimilarProduct } from './ProductSlice'
import fast from '../visules/imgs/fast.png'
import dilivery from '../visules/imgs/payment.png'
import payment from '../visules/imgs/dilivery.png'
import NavBar from '../Naviagations/NavBar';
import '../css/main.css'
import { useNavigate } from 'react-router-dom'


export default function ProducrDetails() {
  const [img,setImg] = useState(0)
  const SelectedProduct = useSelector(SelectProductbyID);
  const SimilarProduct = useSelector(SelectedSimilarProduct);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const handleClick = ({id,category})=>{
    dispatch(FetchProductbyIDAsync({id:id}))
    dispatch(FetchProductbyCategoryAsync({category:category}))
    setTimeout(()=>{ navigate('/products/details')},200)
  }

  const features = [
    {
       icon:payment,
       title:"Secure Transitction"  
    },
  {
       icon:fast,
       title:"Payment Method" 
    },
    {
       icon:dilivery,
       title:"Secure Transitction"
    }
   ]

   const handleOrder = ()=>{
      console.log("Handle Order API Called")
      const price = Math.round(SelectedProduct.price - (SelectedProduct.price/100*SelectedProduct.discountPercentage))
      console.log(price)
      navigate('/order',{state:{id:SelectedProduct._id,price:price,title:SelectedProduct.title}})
   }
   const arr = [];

  for(let i=0;i<SelectedProduct.rating;i++){
     arr.push(1);
  }
  useEffect(()=>{

  },[SelectedProduct])
  return (
    <div>
      <NavBar/>
      {
        !SelectedProduct?<p>Woring</p>:
      
      <div className="pb-20">
      <div className="grid grid-cols-7 px-20 py-20 bg-light-orange-100">
         <div>
            {
               SelectedProduct.images.map((img,index)=>{
                  return (
                     <img src={img} className="pb-2 cursor-pointer" onClick={(e)=> setImg(index)}/>
                  )
               })
            }
         </div>
        <div className="col-span-4 flex justify-center items-center px-20">
         <img src={SelectedProduct.images[img]} className="hover:scale-125 hover:cursor-zoom-in w-[800px]" />
        </div>
        <div className="col-span-2 flex flex-col justify-center">
         <p className="text-gray-green chathura-bold text-5xl">{SelectedProduct.title}</p>
            <div className="pt-4">
         
         {
               arr.map(()=>
                  <span className='text  text-dark-orange-1000 text-xl'>&#9733; </span>
               )
            }
          <span className='charmonman-regular text-2xl text-dark-orange-1000'>{SelectedProduct.reviews} reviews</span>
          </div>

          <p className="rufina-regular text-gray-green pt-4">{SelectedProduct.description}</p>

          <div>
            <div className="flex">
               {
                  features.map((data)=>{
                     return (
                        <div className="flex flex-col justify-center items-center px-2 py-4 cursor-pointer" >
                       <img src={data.icon}/>
                       <p className="text-gray-green text-sm sansita-bold">{data.title}</p>
                       </div>
                     )
                  })
               }
            </div>
            <div className='pt-4'>
            <span className='l line-through pr-2 text-gray-green sansita-regular'>&#x20B9; {SelectedProduct.price}</span>
            <span className='pr-2 text-gray-green sansita-regular'> &#x20B9;{Math.round(SelectedProduct.price - (SelectedProduct.price*SelectedProduct.discountPercentage/100))}</span>
            <span className='text-dark-orange-1000 charmonman-regular text-lg'>{SelectedProduct.discountPercentage}% Save</span>
         </div>
         <div className="flex justify-center gap-10 pt-4">
                <button className="text text-white-shoadow px-5 py-2 shadow-2xl bg-gray-green rounded-full hover:text-gray-green hover:bg-white-shoadow  text-base sansita-regular" onClick={()=> handleOrder()}>
              Shop Now
            </button>
            <button className="text hover:text-white-shoadow px-4 py-2 hover:bg-gray-green rounded-full text-gray-green  shadow-2xl bg-white-shoadow text-base sansita-regular">
              Add To Cart
            </button>
            </div>
          </div>
        </div>

      </div>
      <div>
         <p className="flex justify-center items-center px-20 py-20 text-3xl sansita-regular-italic ">Jewellery related to this</p>
      <div className='grid grid-cols-4 gap-16 px-32'>
          {SimilarProduct.map((data)=>{
             const arr = [];

             for(let i=0;i<data.rating;i++){
                arr.push(1);
             }
            return (
              <div>
                <img src={data.thumbnail} alt={data.title} onClick={()=> handleClick({id:data._id,category:data.category})}/>
                <p className='pt-4 rufina-regular text-sm text-gray-green'>{data.title}</p>
                <div className='pt-2'>

                {
               arr.map(()=>
                  <span className='text  text-dark-orange-1000 text-xl'>&#9733; </span>
               )
            }
          <span className='charmonman-regular text-dark-orange-1000'>{data.reviews} reviews</span>
          </div>
          <div className=''>
            <span className='l line-through pr-2 text-gray-green'>&#x20B9; {data.price}</span>
            <span className='pr-2 text-gray-green sansita-regular'> &#x20B9;{Math.round(data.price - (data.price*data. discountPercentage/100))}</span>
            <span className='text-dark-orange-1000 charmonman-regular text-sm'>{data. discountPercentage}% Save</span>
         </div>
         <div className="flex justify-center gap-10 pt-4">
                <button type='button' className="text text-white-shoadow px-5 py-2 shadow-2xl bg-gray-green rounded-full hover:text-gray-green hover:bg-white-shoadow  text-base sansita-regular" onClick={()=>handleOrder()}>
              Shop Now
            </button>
            <button className="text hover:text-white-shoadow px-4 py-2 hover:bg-gray-green rounded-full text-gray-green  shadow-2xl bg-white-shoadow text-base sansita-regular">
              Add To Cart
            </button>
            </div>
                </div>
            )
          })}
        </div>
      </div>
   </div>}
    </div>
  )
}
