import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { AddtoCart, DeleteCart, FetchCart, UpdateCart } from "./CartAPI";

const initialState = {
   status:"idle",
   cartData:null,
};


export const AddtoCartAsync = createAsyncThunk(
   'Cart/add',
   async (userdata)=>{
      console.log('Add TO Cart API Called');
      const response = await AddtoCart(userdata);
      console.log(response )
      return response.data;
   }
)

export const FetchCartAsync = createAsyncThunk('Cart/cart', async (userdata)=>{
   const response = await FetchCart(userdata);
   return response.data;
})

export const UpdatecartAsync = createAsyncThunk('Cart/update', async (userdata)=>{
   console.log("Update Cart API Called");
   const response = await UpdateCart(userdata);
   return response.data; 
})

export const DeleteCartAsync = createAsyncThunk('Cart/delete', async (userdata)=>{
   const response = await DeleteCart(userdata);
   return response.data;
})


export const CartSlice  = createSlice({
   name:"Cart",
   initialState,
   reducers:{},
   extraReducers:(builder)=>{
      builder
      .addCase(AddtoCartAsync.pending,(state)=>{
         state.status = 'pending';
      })
      .addCase(AddtoCartAsync.fulfilled, (state,actions)=>{
         state.status = 'idle';
         state.cartData.push(actions.payload);
      })
      .addCase(FetchCartAsync.pending,(state)=>{
         state.status = 'pending';
      })
      .addCase(FetchCartAsync.fulfilled,(state,actions)=>{
         state.status = 'idle';
         console.log(actions.payload)
         state.cartData = actions.payload;
      })
      .addCase(UpdatecartAsync.pending,(state)=>{
         state.status = 'pending';
      })
      .addCase(UpdatecartAsync.fulfilled,(state,actions)=>{
         state.status = 'idle';
         console.log(actions.payload)
         const index = state.cartData.findIndex(item=>item._id === actions.payload._id)
         state.cartData[index] = actions.payload;
      })
      .addCase(DeleteCartAsync.pending,(state)=>{
         state.status = 'pending';
      })
      .addCase(DeleteCartAsync.fulfilled,(state,actions)=>{
         state.status ='idle';
         const index = state.cartData.findIndex(item => item._id === actions.payload)
         state.cartData.splice(index,1)
      })
   }
})

export const SelectedCart = (state) => state.Cart.cartData;

export default CartSlice.reducer;