import React from 'react';
import NavBar from '../Naviagations/NavBar';
import {useNavigate} from "react-router-dom"
import { useSelector } from 'react-redux';
import { SelectedCart } from '../Features/Cart/CartSlice';
import Cart_Component from '../Components/Cart_Component';

export default function Cart() {
const cart = useSelector(SelectedCart);
const naviate = useNavigate()
let totalitem= 0;
let totalprice = 0;
  for(let i=0;i<cart.length;i++){
    totalitem = totalitem + cart[i].quantity;
    totalprice = totalprice + Math.round(cart[i].product.price - (cart[i].product.price*cart[i].product.discountPercentage/100))
  }



  return (
    <div>
      <NavBar></NavBar>
      
        {cart.length === 0?<div className='w-full h-[90vh] flex-col justify-center items-center flex'><p>No Any Product Is Added To Cart</p>
        <button className=' text-blue-600' onClick={()=> naviate('/products')}>Continue Shopping</button></div>:<div>
      <div className="bg-light-orange-100 min-h-[70vh] px-96 py-20">

        {
          cart.map((data)=>{
            
            return (
             <Cart_Component data={data}/>
            )
          })
        }

      </div>

      <div className="flex h-40 justify-around items-center">

        <div>
          <div className=" flex athiti-bold  text-lg text-gray-green">
            <p>Total Items:-  &nbsp; </p> <p>{totalitem}</p>
          </div>
          <div className=" flex athiti-bold  text-lg text-gray-green">
            <p>Total Price:-  &nbsp; </p> <p>₹{totalprice}</p>
          </div>
        </div>
        <button className="bg-gray-green py-2 h-12 px-5 rounded-full text-white sansita-bold">Checkout</button>
      </div>
      </div>}
      
    </div>
  )
}
