import React, { useEffect } from "react";
import logo from "../visules/imgs/logo.png";
import { Link, useNavigate } from "react-router-dom";
import PhoneInput from "react-phone-number-input";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import RemoveRedEyeSharpIcon from "@mui/icons-material/RemoveRedEyeSharp";
import VisibilityOffSharpIcon from "@mui/icons-material/VisibilityOffSharp";
import { LoginUSerAsync, logedusererror, loggeduser } from "./AuthSlice";
import { SyncLoader } from "react-spinners";


export default function Login() {
  const [number, setNumber] = useState("");
  const [password, setPassword] = useState("");
  const [seepassword, setSeePassword] = useState(false);
  const [loading, setloading] = useState(false);
  const dispatch = useDispatch();
  const user = useSelector(loggeduser);
  const error = useSelector(logedusererror);
  const navigate = useNavigate("");

  function check() {
    console.log(user)
    if (user) navigate("/");
    else {
      console.log(user)
      alert(
        "Something Went Wrong try TO forget Password or create New Account"
      );
      navigate("/signup");
      setloading(!loading);
    }
  }

  const handleSubmit =  async (e) => {
    e.preventDefault();
    if (number === "" || number === undefined) {
      alert("Please Enter Your Number");
      return;
    }
    setloading(!loading);
   await dispatch(LoginUSerAsync({ Phone_Number: number, password: password }));  
    
    setTimeout(()=>{navigate('/check')},200)
    console.log(user)
  };

  

  return (
    <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-sm">
        <img className="mx-auto h-24 w-auto" src={logo} alt="Your Company" />
        <h2 className="mt-10 text-center text-2xl font-bold leading-9 tracking-tight text-gray-900">
          Sign in to your account
        </h2>
      </div>

      <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
        <form className="space-y-6" onSubmit={handleSubmit}>
          <div>
            <label
              htmlFor="email"
              className="block text-sm font-medium leading-6 text-gray-900"
            >
              Phone Number
            </label>
            <div className="mt-2">
              <PhoneInput
                defaultCountry="IN"
                value={number}
                onChange={setNumber}
              />
            </div>
          </div>

          <div>
            <div className="flex items-center justify-between">
              <label
                htmlFor="password"
                className="block text-sm font-medium leading-6 text-gray-900"
              >
                Password
              </label>
              <div className="text-sm">
                <a
                  href="#"
                  className="font-semibold text-indigo-600 hover:text-indigo-500"
                >
                  Forgot password?
                </a>
              </div>
            </div>
            <div className="mt-2 flex justify-start items-center rounded-md border-0  text-gray-900 shadow-sm ring-1  ring-gray-300 placeholder:text-gray-400  focus:ring-inset  sm:text-sm sm:leading-6">
              <input
                id="password"
                name="password"
                type={seepassword ? "text" : "password"}
                autoComplete="current-password"
                pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"
                onChange={(e) => setPassword(e.target.value)}
                required
                className=" w-5/6 border-none rounded-md focus:border-none  text-gray-900 "
              />

              {!seepassword ? (
                <div
                  onClick={() => setSeePassword(!seepassword)}
                  className="m-auto"
                >
                  <RemoveRedEyeSharpIcon
                    style={{ fill: "#3B606A", fontSize: "1.7rem" }}
                  />{" "}
                </div>
              ) : (
                <div
                  onClick={() => setSeePassword(!seepassword)}
                  className="m-auto"
                >
                  <VisibilityOffSharpIcon
                    style={{
                      fill: "#3B606A",
                      width: "3rem",
                      fontSize: "1.7rem",
                    }}
                    className="m-auto"
                  />
                </div>
              )}
            </div>
          </div>

          <div className=" flex justify-center items-center">
            {!loading ? (
              <button
                type="submit"
                className="flex w-full justify-center rounded-md bg-gray-green px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-gray-green-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Sign in
              </button>
            ) : (
              <SyncLoader color="#3B606A" size={12} />
            )}
          </div>
        </form>

        <p className="mt-10 text-center text-sm text-gray-500">
          Not a Account?{" "}
          <Link
            to="/signup"
            className="font-semibold leading-6 text-gray-green"
          >
            Create an Account
          </Link>
        </p>
      </div>
    </div>
  );
}
