import React, { useEffect, useState } from "react";
import NavBar from "../Naviagations/NavBar";
import Product_Componet from "../Components/Product_Componet";
import { ChevronLeftIcon, ChevronRightIcon } from '@heroicons/react/20/solid'
import { useDispatch, useSelector } from "react-redux";
import { FetchProductsAsync, SelectTotalItems, selectedProduct } from "../Products/ProductSlice";
import { Fragment, } from 'react'
import {  Menu, Transition } from '@headlessui/react'
import { ChevronDownIcon} from '@heroicons/react/20/solid'


const sortOptions = [
  { name: 'Best Rating', sort: "rating", order:"desc", current: false },
  { name: 'Price: Low to High', sort: "price", order: "asc", current: false },
  { name: 'Price: High to Low', sort: "price" ,order:"desc", current: false },
]

function classNames(...classes) {
  return classes.filter(Boolean).join(' ')
}

export default function Products() {
  const [page,setPage]= useState(1);
  const [sort,setSort]= useState({});
  const pageSize =10;
  const Toatalitems = useSelector(SelectTotalItems)
  const products = useSelector(selectedProduct);
  const totalpage = Math.ceil(Toatalitems/pageSize);
  const dispatch = useDispatch();



const handlesort = (e,option)=>{
  const sort = {_sort:option.sort,_order:option.order};
  setSort(sort)

 }

const handlepagination = (e,index)=>{

  setPage(index+1)
}

useEffect(()=>{
  const pagination = {_page:page,_limit:pageSize}

  dispatch(FetchProductsAsync({sort,pagination}))
},[dispatch,sort,page])
 

   
  return (
    <div>
      <NavBar></NavBar>
      <div className="bg-white">
    <div>
      {/* Mobile filter dialog */}
      

      <main className="mx-auto max-w-7xl ">
        <div className="flex justify-end   pt-3 items-end">
          <div className="flex items-center">
            <Menu as="div" className="relative inline-block text-left">
              <div>
                <Menu.Button className="group inline-flex justify-center text-sm font-medium text-gray-700 hover:text-gray-900">
                  Sort
                  <ChevronDownIcon
                    className="-mr-1 ml-1 h-5 w-5 flex-shrink-0 text-gray-400 group-hover:text-gray-500"
                    aria-hidden="true"
                  />
                </Menu.Button>
              </div>

              <Transition
                as={Fragment}
                enter="transition ease-out duration-100"
                enterFrom="transform opacity-0 scale-95"
                enterTo="transform opacity-100 scale-100"
                leave="transition ease-in duration-75"
                leaveFrom="transform opacity-100 scale-100"
                leaveTo="transform opacity-0 scale-95"
              >
                <Menu.Items className="absolute right-0 z-10 mt-2 w-40 origin-top-right rounded-md bg-white shadow-2xl ring-1 ring-black ring-opacity-5 focus:outline-none">
                  <div className="py-1">
                    {sortOptions.map((option) => (
                      <Menu.Item key={option.name}>
                        {({ active }) => (
                          <p
                            onClick={e =>handlesort(e,option)}
                            className={classNames(
                              option.current ? 'font-medium text-gray-900' : 'text-gray-500',
                              active ? 'bg-gray-100' : '',
                              'block px-4 py-2 text-sm'
                            )}
                          >
                            {option.name}
                          </p>
                        )}
                      </Menu.Item>
                    ))}
                  </div>
                </Menu.Items>
              </Transition>
            </Menu>

          </div>
        </div>

       
      </main>
    </div>
  </div>
      <div>
        <div className="grid mt-12 grid-cols-4 gap-x-5 gap-y-20 px-40 py-20 bg-light-orange-100">
          { products.map((data)=>{
            return  <Product_Componet data={data} className={""}/>
          })

          }
           
        </div>
        <div className="px-40">
        <div className="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6">
      <div className="flex flex-1 justify-between sm:hidden">
        <a
          href="#"
          className="relative inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Previous
        </a>
        <a
          href="#"
          className="relative ml-3 inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Next
        </a>
      </div>
      <div className="hidden sm:flex sm:flex-1 sm:items-center sm:justify-between">
        <div>
          <p className="text-sm text-gray-700">
            Showing <span className="font-medium">{(page-1)*pageSize +1}</span> to <span className="font-medium">{page*pageSize>Toatalitems?Toatalitems:page*pageSize}</span> of{' '}
            <span className="font-medium">{Toatalitems}</span> results
          </p>  
        </div>
        <div>
          <nav className="isolate inline-flex -space-x-px rounded-md shadow-sm" aria-label="Pagination">
            <div
              onClick={(e)=>setPage(page-1)}
              className={`relative  cursor-pointer ${page ===1?'hidden':'block'} inline-flex items-center rounded-l-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0`}
            >
              <span className="sr-only">Previous</span>
              <ChevronLeftIcon className="h-5 w-5" aria-hidden="true" />
            </div>

            {
              Array.from({length:totalpage}).map((el,index)=>(
                <div
                  
              onClick={e=>handlepagination(e,index)}
              aria-current="page"
              className={`relative cursor-pointer z-10 inline-flex items-center ${index+1 === page?'bg-gray-green text-white':'bg-white text-gray-green'} px-4 py-2 text-sm font-semibold  focus:z-20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600`}
            >
              {index+1}
            

                </div>
              ))
            }
           
           
            
            
           
            
           
            <div
            onClick={(e)=>setPage(page+1)}
             
              className={`relative inline-flex ${page===totalpage?'hidden':'block'} items-center rounded-r-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0`}
            >
              <span className="sr-only">Next</span>
              <ChevronRightIcon className="h-5 w-5" aria-hidden="true" />
            </div>
          </nav>
        </div>
      </div>
    </div>
        </div>
      </div>
    </div>
   
  );
}
