import React from "react";
import "../css/main.css";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useDispatch, useSelector } from "react-redux";
import { FetchProductbyCategoryAsync, FetchProductbyIDAsync } from "../Products/ProductSlice";
import {  useNavigate } from "react-router-dom";
import { loggeduser } from "../Authentication/AuthSlice";
import { AddtoCartAsync } from "../Features/Cart/CartSlice";

export default function Product_Componet({ data }) {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const user = useSelector(loggeduser)

  const arr = [];
  for (let i = 0; i < data.rating; i++) {
    arr.push(1);
  }

  const handleClick = ()=>{
    dispatch(FetchProductbyIDAsync({id:data._id}))
    dispatch(FetchProductbyCategoryAsync({category:data.category}))
    setTimeout(()=>{ navigate('/products/details')},200)
  }

  const handleCart = ()=>{
    if(!user){
      console.log('handleCart called')
      navigate('/login');
      return ;
    }
    dispatch(AddtoCartAsync({user:user.id,quantity:1,product:data._id}))
    toast("Your Product is Added To cart");
   return ;
  }

  return (
    <div>
        <ToastContainer
        position="top-right"
        autoClose={2000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
      <img src={data.thumbnail} className="w-[270px] h-[270px]" onClick={()=>handleClick()} />
      <p className="pt-4 text-xs">{data.title}</p>
      <div className="flex items-end">
        {arr.map(() => (
          <span className="text text-light-orange text-2xl">&#9733;</span>
        ))}

        <p className="pl-2 text-light-orange charmonman-regular-italic">
          {data.reviews} reviews
        </p>
      </div>
      <div className="flex gap-2  ">
        <p className="line-through sansita-regular text-gray-green">
          &#x20B9; {data.price}
        </p>
        <p className="sansita-regular text-gray-green">&#x20B9; {Math.round(data.price - data.price * data.discountPercentage/100)}</p>
        <p className="charmonman-regular-italic text-light-orange">Save {data.discountPercentage}%</p>
      </div>
      <div className="flex justify-center gap-2 pt-6">
        <button className="px-6 py-2 rounded-full sansita-regulart text-base bg-Buttom-color text-gray-green shadow-xl">
          Buy Now
        </button>
        <button className="px-6 py-2 rounded-full sansita-regular text-base bg-gray-green text-Buttom-color shadow-xl" onClick={(e) => handleCart()}>
          Add To Cart
        </button>
      </div>
    </div>
  );
}
