import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { CreateAccount, LoginUser, CheckUser, UpdatePassword } from "./AuthAPI";

const initialState = {
  status: "idle",
  user: null,
  error: null,
  existence:false,
  Profile :"order",
};

//Function To call CreateUser Function it was dispatched from component of our APP
export const CreateUserAsync = createAsyncThunk(
  "Auth/CreateAccount",
  async (userdata) => {
    console.log("Working ", userdata)
    const response = await CreateAccount(userdata);
    return response.data;
  }
);

export const ChangeProfileNav = createAsyncThunk(
  "Auth/Changenav",
  async (userdata) => { 
    console.log("Working", userdata)
    return userdata
  }
);

//Function TO call LoginUser Function it Was Dispacthed From Component OF our App
export const LoginUSerAsync = createAsyncThunk(
  "Auth/LoginUser",
  async (userdata, { rejectWithValue }) => {
    try {
      const response = await LoginUser(userdata);
      return response.data;
    } catch (err) {
      return rejectWithValue(err);
    }
  }
);
//Function To call CheckUser Function it Was Dispatched From Component of Our APP

export const CheckUserAsync = createAsyncThunk("Auth/CheckUser", async (userdata) =>{
  console.log('checkuserAsync function called')
  const response = await CheckUser(userdata)
  return response.data;
})

//Function To Call UpdatePassword Function it Was Dispatched From of Our APP

export const UpdatePasswordAsync = createAsyncThunk('Auth/Updatepassword', async (userdata)=>{
  const response = await UpdatePassword(userdata);
  return response.data;
})

export const AuthSlice = createSlice({
  name: "Auth",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(CreateUserAsync.pending, (state) => {
        state.status = "Pending";
      })
      .addCase(CreateUserAsync.fulfilled, (state, actions) => {
        state.status = "idle";
        state.user = actions.payload;
      })
      .addCase(LoginUSerAsync.pending, (state) => {
        state.status = "pending";
      })
      .addCase(LoginUSerAsync.fulfilled, (state, actions) => {
        console.log("User found this ", actions.payload);
       state.user = actions.payload
        
      })
      .addCase(LoginUSerAsync.rejected, (state, action) => {
        console.log("user Not Found", action.payload);
        state.status = "idle";
        state.error = action.payload;
      })
      .addCase(CheckUserAsync.pending, (state)=>{
        state.status = 'pending';
      })
      .addCase(CheckUserAsync.fulfilled,(state,action)=>{
        state.status = 'idle';
        state.existence = action.payload;
      })
      .addCase(CheckUserAsync.rejected, (state)=>{
        state.status = 'idle';
        state.existence = true;
      })
      .addCase(UpdatePasswordAsync.pending, (state) =>{
        state.status = 'pending';
      })
      .addCase(UpdatePasswordAsync.fulfilled, (state,actions)=>{
        state.status = 'idle';
        state.user = actions.payload;
      })
      .addCase(UpdatePasswordAsync.rejected, (state,actions)=>{
        state.status ='idle';
        state.error =actions.payload;
      })
      .addCase(ChangeProfileNav.fulfilled,(state, action)=>{
        state.Profile =action.payload 
      });
  },
});

export const loggeduser = (state) => state.Auth.user;
export const logedusererror = (state) => state.Auth.error;
export const userexist  = (state) => state.Auth.existence;
export const Profile_nav = (state) => state.Auth.Profile;

export default AuthSlice.reducer;
